@import url(https://fonts.googleapis.com/css2?family=Benne&display=swap);
@import url(https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.wrapper
{
  display: flex;
  justify-content: space-around;
  height: 100vh;
  background-color: midnightblue;
}
.topnav
{
  background-color: black;
  padding: 1%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo
{
  color: mediumspringgreen;
  text-align: left;
  font-family: 'RocknRoll One', sans-serif;
  font-weight: bold;
  font-size: 4vh;
}

.logo2
{
  color: white;
  text-align: left;
  font-family: 'RocknRoll One', sans-serif;
  font-weight: bold;
  font-size: 1vh;
}

.heading
{
	margin: 1% auto;
  color: black;
  background-color: mediumspringgreen;
  text-align: left;
  font-weight: bold;
  padding:1%;
  font-size: 2vh;
  border-radius: 10%;
}
.container
{
  padding: 1%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 100%;
  background-color: white;
}

button
{
  margin: 1% auto;
  color: black;
  background-color: mediumspringgreen;
  text-align: left;
  font-weight: bold;
  font-size: 2vh;
  border: none;
  padding:1%;
  border-radius: 10%;
}

.content
{
	border: 5px solid mediumspringgreen;
	height: 90%;
	padding: 2%;
	background-color:white;
	overflow: scroll;
	max-width: 1000px;
}
.code
{
 overflow: auto;
 outline: none;
  width: 100%;
  height: 90vh;
  padding:1%;
  border: none;
  line-height: 1.8;
  max-width: 900px;
}
input
{
 margin: 1% auto;
  color: black;
  text-align: left;
  font-weight: bold;
  font-size: 2vh;
  border: none;
  border-bottom: 2px solid mediumspringgreen ;
  padding:1%;
  outline: none;
}
